import { loadStyles } from 'load-themed-styles';

loadStyles('.ms-GettingStartedPage{margin-top:-20px}.ms-GettingStartedPage h1{color:"[theme:neutralPrimary, default: #333333]";font-family:"Segoe UI Light WestEuropean","Segoe UI Light","Segoe UI",Tahoma,Arial,sans-serif;font-size:28px;font-weight:400}.ms-GettingStartedPage h2{color:"[theme:neutralPrimary, default: #333333]";font-family:"Segoe UI Light WestEuropean","Segoe UI Light","Segoe UI",Tahoma,Arial,sans-serif;font-size:21px;font-weight:400}.ms-GettingStartedPage h3{color:"[theme:neutralPrimary, default: #333333]";font-family:"Segoe UI Semilight WestEuropean","Segoe UI Semilight","Segoe UI",Tahoma,Arial,sans-serif;font-size:17px;font-weight:400}.ms-GettingStartedPage-banner{background:"[theme:neutralLight, default: #eaeaea]";color:#fff;padding:1px 20px;margin:-20px;margin-bottom:20px;border-bottom:1px solid "[theme:neutralTertiaryAlt, default: #c8c8c8]"}.ms-GettingStartedPage-banner h1{margin-bottom:0}.ms-GettingStartedPage-banner h3{margin-top:0}@media (max-width:1023px){.ms-GettingStartedPage-banner{margin:-20px 0;margin-bottom:20px}}.ms-GettingStartedPage-code{background:"[theme:neutralLighter, default: #f4f4f4]";padding:20px}.ms-GettingStartedPage-code pre{margin:0}.ms-GettingStartedPage-code .hljs{background:inherit;padding:0}');



