const BEEP_DATA = 'data:audio/wav;base64,';

const MIN_DURATION_BETWEEN_BEEPS = 100;

let _beepsLeft = 0;

export function beep() {
  _beepsLeft++;
  if (_beepsLeft === 1) {
    playBeep();
  }
}

function playBeep() {
  if (_beepsLeft) {
    const beep = new Audio(BEEP_DATA);
    beep.play();

    setTimeout(() => {
      _beepsLeft--;
      playBeep();
    }, MIN_DURATION_BETWEEN_BEEPS);
  }
}



